	module.exports = {"data":{"wordpressPost":{"title":"Eyes on Diabetes &#8211; World Diabetes Day 2016","content":"<p>&nbsp;</p>\n<p>World Diabetes Day (WDD) is celebrated by million<a href=\"http://blogs.bmj.com/bmjopen/files/2016/11/WDD-logo-date-EN.png\"><img class=\"wp-image-1053 alignright\" src=\"http://blogs.bmj.com/bmjopen/files/2016/11/WDD-logo-date-EN.png\" alt=\"wdd-logo-date-en\" width=\"253\" height=\"164\" /></a>s of people around the world on November 14<sup>th </sup>each year. [1] Led by the IDF, WDD is a global campaign that aims to raise awareness of diabetes and promote efforts to tackle the condition as a global health priority. [2]</p>\n<p>The International Diabetes Federation (IDF) estimate that 415 million adults were living with diabetes in 2015, with this number set to rise to around 642 million by 2040. [3] This is equivalent to one in 10 adults. [3]</p>\n<p>Diabetes is the leading cause of cardiovascular disease, blindness and kidney failure in many countries around the world, but some of these complications, such as blindness, can be prevented with early detection and treatment. [3]</p>\n<p>This year, the theme of WDD is “eyes on diabetes.” [2] Key messages of the campaign are:</p>\n<ul>\n<li>Screening for type 2 diabetes is essential for managing the condition and reducing the risk of complications.</li>\n<li>Screening for diabetes complications is a vital for the management of both types of diabetes. [2]</li>\n</ul>\n<p>Highlighted articles from BMJ Open for World Diabetes Day 2016 include:</p>\n<ul>\n<li><a href=\"http://dx.doi.org/10.1136/bmjopen-2015-009116\">Informed shared decision-making programme on the prevention of myocardial infarction in type 2 diabetes: a randomised controlled trial</a></li>\n<li><a href=\"http://dx.doi.org/10.1136/bmjopen-2015-010155\">Social inequalities in prevalence of diagnosed and undiagnosed diabetes and impaired glucose regulation in participants in the Health Surveys for England series</a></li>\n<li><a href=\"http://dx.doi.org/10.1136/bmjopen-2015-010007\">Sex differences in body anthropometry and composition in individuals with and without diabetes in the UK Biobank</a></li>\n<li><a href=\"http://dx.doi.org/10.1136/bmjopen-2015-008422\">Investigation of the relationship between patient empowerment and glycaemic control in patients with type 2 diabetes: a cross-sectional analysis.</a></li>\n<li><a href=\"http://bmjopen.bmj.com/content/6/9/e010524.full\">Is self-monitoring of blood glucose effective in improving glycaemic control in type 2 diabeteswithout insulin treatment: A meta-analysis of randomised controlled trials  </a></li>\n</ul>\n<p>&nbsp;</p>\n<p><strong>Sources</strong></p>\n<ol>\n<li>World diabetes day 2016. Diabetes UK. <a href=\"https://www.diabetes.org.uk/Get_involved/World-Diabetes-Day/\">https://www.diabetes.org.uk/Get_involved/World-Diabetes-Day/</a>, accessed 8 November 2016</li>\n<li>World diabetes day. IDF. <a href=\"http://www.idf.org/wdd-index/about.html\">http://www.idf.org/wdd-index/about.html</a>, accessed 31 October 2016</li>\n<li>World diabetes day 2016. IDF.<a href=\"http://IDF. http://www.idf.org/wdd-index/wdd2016.html\"> http://www.idf.org/wdd-index/wdd2016.html </a>accessed, 31 October 2016</li>\n</ol>\n<p><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"bfccac35-59f9-5787-9d55-e588ee7b3b4b"}}

